.header-container{
    display: flex;
    width: 100%;
    background-color: #e2e0e0db;
    align-items: center;
    padding: 0 7%;
    height: 80px;
    position: relative;
}

.logo-container{
    position: relative;
    left: 0%;
}

.lookfor-container{
    position: relative;
}

.login-container{
    display: inline-flex;
    position: absolute;
    right: 5%;
}

/*logo*/
.logo{
    font-size: 3rem;
    line-height: 1;
    font-weight: 700;
    font-family: 'Roboto Slab', serif;
    margin-right: 2rem;    
}

/*lookfor*/
.input-look-for{
    border: 0.1px solid var(--gris3);
    padding: 1rem;
    height: 31.4px;
    border-radius: 7px 0px 0px 7px;
    min-width: 300px;
}
   
.button-look-for{
    height: 30px;
    width: 31px;
    display: inline-block;
    background-size: 3rem;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    position: relative;
    right: 0rem;
    top: 0px;
    background-color: white;
    border: none;
    text-indent: -9999px;
    border-radius: 0px 5px 5px 0px;
    border-top: 1px;
    border-right: 1px;
    border-bottom: 1px;
    border-left: 1px solid #c7c7c7;
}
.button-look-for:hover {
    cursor: pointer;
}


/*login*/
.login-container p {
    margin-top: auto;
    margin-right: 1rem;
    margin-bottom: auto;
    margin-left: 1rem;
}


/* nav */
.navbar-container{
    background-color: #7ad274;
    text-align: center;
    border-radius: 0px 0px 18px 18px;
    padding-left: 2rem;
  
}

.navbar-container a {
    font-size: 1.8rem;
    margin-left: 2rem;
    color: #ffffff;
    font-family: 'PT Sans', sans-serif;
}

.navbar-container a:last-of-type {
    margin-right: 0;
}














